<template>
    <div class="page-index">
        <van-nav-bar            
            left-arrow
            title="修改昵称"
            @click-left="onClickLeft"
            @click-right="onClickRight"
            right-text="保存"
        >
          
        </van-nav-bar>
        <section class="main">
            <p class="label">我的昵称</p>
            <van-cell-group :border="false">
                <van-field class="username" type="textarea" maxlength="20" v-model.trim="username" placeholder="请输入昵称"  />
            </van-cell-group>
            <van-divider />
            <p class="count">{{username.length}}/20</p>
        </section>
    </div>
</template>

<script>
import { Toast } from 'vant';
export default {
    data(){
        return {
            username:''
        }   
    },
    created(){
        this.getUserInfo();
    },
    methods:{
        getUserInfo(){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });
            this.api.getUserInfo({
                token:this.$store.state.userData,
                uid:''
            }).then( res => {
                Toast.clear();
                if(res.err_code == 0){
                    this.username = res.data.username;
                   
                }
            })
        },
        saveMyInfoByUsername(){
            Toast.loading({
                message: '加载中...',
                forbidClick: true
            });
            this.api.saveMyInfoByUsername({
                token:this.$store.state.userData,
                username:this.username
            }).then( res => {
                Toast.clear();
                if(res.err_code == 0){
                    this.$router.go(-1)
                   
                }else {
                    return Toast(res.err_msg);
                }
            })
        },
        onClickLeft(){
            this.$router.go(-1)
        },
        onClickRight(){
            if(!this.username.length){
               return Toast('昵称不能为空');
            }
            this.saveMyInfoByUsername();
        }
    }
}
</script>

<style lang="less" scoped>
.page-index{
    .main{
        padding: 38px 15px 0px;

        .label{
            font-size: 13px;
            
            color: #4E5578;
            margin-bottom: 30px;
        }
        .username{
            font-size: 18px;
            color: #D1D1D1;
            margin: 0 -15px;
            margin-bottom: 45px;
        }
        .count{
            color: #D1D1D1;
            font-size: 13px;
        }
    }
}
</style>